// Interfaces
import {
	IELearningAuth,
	ELearningAuthSession,
	ELearningGetSessionResult,
} from "@/interfaces/elearning/auth";

// Libraries
import * as cheerio from "cheerio";
import { CheerioAPI } from "cheerio";

// Helpers
import request from "@/helpers/request";
import { config } from "@/constants";

export class Auth implements IELearningAuth {
	protected getCsrfLoginElement(parsedHtml: CheerioAPI) {
		return parsedHtml("input[name=logintoken]").first();
	}
	protected getSessionVerifyLinkElement(parsedHtml: CheerioAPI) {
		return parsedHtml("a")
			.filter((_, el) => {
				return el.attribs.href.indexOf("testsession") > -1;
			})
			.first();
	}
	protected getSessionFromCookies(cookieStrings: string[] | undefined) {
		if (!cookieStrings)
			throw new Error(`Error parsing session from cookie, given empty cookies`);
		const filteredCookies = cookieStrings.filter(
			(cookieString) => cookieString.toLowerCase().indexOf("moodlesession") > -1
		);
		if (filteredCookies.length === 0)
			throw new Error(`No session found in cookies`);
		const sessionCookie = filteredCookies[0];
		const session = sessionCookie.match(/MoodleSession=([^;]*)/);
		if (!session)
			throw new Error(
				`Error parsing session from cookie, invalid session cookie`
			);
		return session[1];
	}
	protected getSession(): Promise<ELearningGetSessionResult> {
		return new Promise(async (resolve, reject) => {
			try {
				const response = await request(`${config.BASE_URL}/login/index.php`);
				const htmlText = await response.data;
				const cookieStrings = response.headers["set-cookie"];
				const session = this.getSessionFromCookies(cookieStrings);
				const parsedHtml = cheerio.load(htmlText);
				const csrfToken = this.getCsrfLoginElement(parsedHtml).val();
				if (typeof csrfToken !== "string")
					throw new Error("CSRF token not found");
				return resolve({
					token: csrfToken,
					session,
				});
			} catch (err) {
				return reject(err);
			}
		});
	}
	public async login(
		username: string,
		password: string
	): Promise<ELearningAuthSession> {
		try {
			const { token, session } = await this.getSession();
			const response = await request(
				"https://elearning.ut.ac.id/login/index.php",
				{
					headers: {
						"content-type": "application/x-www-form-urlencoded",
						cookie: `MoodleSession=${session}`,
					},
					data: `anchor=&logintoken=${token}&username=${username}&password=${password}`,
					method: "POST",
					maxRedirects: 0,
				}
			);
			const isInvalidLogin = response.headers["location"]
				? response.headers["location"]!.indexOf("testsession") === -1
				: true;
			if (isInvalidLogin)
				throw new Error(
					"Login failed, please check your username and password"
				);
			const cookieStrings = response.headers["set-cookie"];
			const authSession = this.getSessionFromCookies(cookieStrings);
			return Promise.resolve(authSession);
		} catch (err) {
			return Promise.reject(err);
		}
	}
}

export default Auth;
